import React from "react"
import styles from "./LoginButton.module.css"
import classNames from "classnames/bind"

const cx = classNames.bind(styles)//module.css  classNames  


class LoginButton extends React.Component {
    state = {
       loading: false 
    }

     

    startLoading = () => {
      this.setState({
        loading: true,
      })
      setTimeout(()=>{
        this.setState({
            loading: false,
        })
      },1000)
    }


    render() {
        return <button onClick={this.startLoading} className={cx("button", {loading: this.state.loading})}> </button>
    }
   
}

export default LoginButton